.coaches {
  min-height: 530px;
  background-color: $color-default-white;
  font-family: $ff-roboto-condensed;

  @include vp-767 {
    min-height: 450px;
  }
}

.coaches__wrapper {
  position: relative;
  margin: 0 auto;
  padding: 76px 0 105px 0;
  width: 100%;

  @include vp-1199 {
    padding-bottom: 100px;
  }

  @include vp-767 {
    width: 86.5%;
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.coaches__title {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  letter-spacing: 1.62px;
  margin: 0;
  margin-bottom: 68px;
  padding-left: 4px;

  @include vp-1199 {
    font-size: 42px;
    text-align: center;
    margin-bottom: 45px;
    padding: 0;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
    margin-bottom: 50px;
  }
}

.coaches__slider {
  position: relative;

  @include vp-1199 {
    width: 83.5%;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 85%;
    margin: 0 auto;
  }
}

.coaches__button {
  position: absolute;
  top: 50%;
  padding: 9px 9px 9px 5px;

  &--prev {
    right: 200%;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: transparent;
  }

  &--next {
    left: 200%;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: transparent;
  }

  @include vp-1199 {
    &--prev {
      right: 0;
    }

    &--next {
      left: 0;
    }
  }

  @include vp-767 {
    left: -12%;
  }

  &--next {
    transform: rotate(180deg);
    top: 42%;
    right: -5%;

    @include vp-1199 {
      right: 7%;
    }

    @include vp-1199 {
      right: 3%;
    }
  }
}

.coaches__list {
  width: 100%;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;

  @include vp-1199 {
    width: 100%;
  }

  @include vp-1199 {
    width: 100%;
  }
}

.coaches__item {
  position: relative;
  width: 260px;
  margin: 0;
  color: $color-default-white;

  @include vp-1199 {
    width: 268px;
  }

  @include vp-767 {
    width: 85%;
  }
}

.coaches__item .swiper-slide {
  @include vp-1199 {
    width: 268px;
  }
}

.coaches__image {
  @include vp-1199 {
    height: 294px;
    width: 268px;
    margin: 0;
  }

  @include vp-767 {
    width: 100%;
    height: 274px;
  }
}

.coaches__image img {
  @include vp-1199 {
    height: 294px;
    width: 268px;
  }

  @include vp-767 {
    width: 100%;
    min-height: 274px;
  }
}

.coaches__item-title {
  font-size: 18px;
  line-height: 150%;
  position: absolute;
  bottom: 4px;
  left: 78px;

  &--lavrov {
    left: 69px;
  }

  @include vp-1199 {
    bottom: 1.5%;
    left: 31.9%;
  }
}

.coaches__info {
  position: absolute;
  color: $color-transparent;
  top: 0;
  left: 0;
  width: 260px;
  height: 294px;

  @include vp-1199 {
    width: 268px;
  }

  @include vp-767 {
    width: 100%;
    min-height: 274px;
  }
}

.coaches__info-achivements  {
  position: relative;
}

.coaches__item:hover .coaches__info {
  position: absolute;
  height: 294px;
  padding: 52px 57px;
  background-color: rgba(16, 38, 84, 0.89);
  color: $color-default-white;
  top: 0;
  left: 0;

  @include vp-1199 {
    // width: 100%;
  }

  @include vp-767 {
    width: 100%;
    height: 274px;
    padding: 42px 40px;
  }
}

.coaches__item:hover .coaches__info-achivements-item--one {
  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    background-color: $color-default-white;
    border-radius: 50%;
    top: 9px;
    left: -2px;

    @include vp-767 {
      left: -3.6%;
    }
  }
}

.coaches__item:hover .coaches__info-achivements-item--two {
  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    background-color: $color-default-white;
    border-radius: 50%;
    bottom: 64px;
    left: -2px;

    @include vp-767 {
      left: -3.6%;
    }
  }
}

.coaches__item:hover .coaches__info-achivements-item--three {
  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    background-color: $color-default-white;
    border-radius: 50%;
    bottom: 10px;
    left: -2px;

    @include vp-767 {
      left: -3.6%;
      bottom: 9px;
    }
  }
}

.coaches__item:hover .coaches__item-title {
  display: none;
}

.coaches__info-title {
  font-size: 18px;
  line-height: 150%;
  margin: 0;
}

.coaches__info-feature {
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  display: block;
  margin-top: -4px;
  margin-bottom: 33px;
}

.coaches__info-achivements {
  position: relative;
  margin: 0;
  padding: 0;
  padding-left: 15px;
  width: 165px;
  list-style: none;
  font-size: 14px;
  line-height: 150%;
  display: flex;
  flex-direction: column;
  row-gap: 14px;

  @include vp-767 {
    padding-left: 10px;
  }
}
